<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="用户名称" :inline="true">
                    <el-input v-model.trim="filters.user_name" placeholder="请输入用户名称"></el-input>
                </el-form-item>
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/userManagement/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="user_name" label="用户名称" width="120">
                </el-table-column>
                <el-table-column prop="user_pass" label="密码" width="120">
                </el-table-column>
                <el-table-column prop="user_real_name" label="姓名" width="120">
                </el-table-column>
                <el-table-column prop="user_phone" label="联系电话">
                </el-table-column>
                <el-table-column prop="cp_name" label="所属公司">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="pos_name" label="岗位">
                </el-table-column>
                </el-table-column>
                <el-table-column label="更新时间">
                    <template slot-scope="{row, $index}">
                        {{row.update_time}}
                    </template>
                </el-table-column>
                <el-table-column prop="update_user_name" label="更新人">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row,$index}">
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/userManagement/edit', query: {id:row.user_id}})">修改</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                loading: false,
                filters: {
                    user_name:''
                }
            }
        },
        created() {
            this.getData();
        },
        computed: {

        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.userManagementList(params).then(res => {
                    this.loading = false
                    if(res.code == 0){
                        this.tableData = res.data;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                API.userManagementDel({id: row.user_id}).then(res => {
                    if(res.code == 0){
                        this.tableData.splice(index, 1);
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>

<style scoped>

</style>
